header {
    background: #DCEDC8;
    padding-top: 20px;
    height: 100px;
    font-size: 24px;
    text-transform: uppercase;

    position: -webkit-sticky;
    position: sticky;
    top: 0;
}
.header-bar {
    margin: 0 auto;
    text-align: center;
}
.header-items {
    display: inline-block;
}


