footer {
    background: #DCEDC8;
    height: 100px;
    margin-top: 50px;
    text-align: center;

    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
}
footer i {
    color: #000;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}


